import { render } from "@wordpress/element";
import App, { DYNAMIC_PLAYER_EMBED_ID } from "./App";
window.addEventListener("DOMContentLoaded", function (evt) {
    var wrapper = document.getElementById(DYNAMIC_PLAYER_EMBED_ID);
    if (wrapper) {
        var playerEmbed = wrapper.dataset.playerEmbed;
        render(React.createElement(App, { loadHypothesis: true, playerEmbed: playerEmbed }), wrapper);
    }
});
